import React, { Component } from 'react';
import TaskData from '../entities/TaskData';
import { Button, LinearProgress } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import settings from './sphere.settings';
import {computeDist} from '../../utils/calcs';

const BorderLinearProgress = withStyles({
    root: {
        height: 10,
        backgroundColor: "rgb(255, 54, 40, 0.3)",
    },
    bar: {
        borderRadius: 20,
        backgroundColor: 'rgb(255, 54, 40)',
    },
})(LinearProgress);

const getRandomArbitrary = (min, max) =>  Math.random() * (max - min) + min;

class Tarefa extends Component {

    constructor(props) {
        super(props);
        this.ntrials = props.ntrials
        this.debug = window.qparsed.debug === "1"

        this.state = {
            randomPos: [getRandomArbitrary(-90,90), getRandomArbitrary(-180,180)],
            enabled: true,
            waiting: false,
            lastMove: [0, 0]
        }

        this.answers = []
        this.clicks = 0
        this.limit = this.debug ? 4 : 20

        this.handleNext = this.handleNext.bind(this)
        this.nextState = 'fim';
    }

    changeTarget = () => {
        const randomPos = [getRandomArbitrary(-90,90), getRandomArbitrary(-180,180)];
        this.setState({ randomPos })
        window.openMap(settings, {
            initial: randomPos,
            onDoubleClick: this.click,
            onMove: this.mouseMove
        });
    }

    componentDidMount() {
        this.changeTarget()
    }

    click = (lat,long) => {
        const clickPos = [lat, long]
        const { randomPos } = this.state
        const distancia = computeDist(randomPos, clickPos)

        const answer = {
            aleatorio: randomPos,
            click: clickPos,
            distancia: distancia,
            score: 1 - (distancia/Math.PI)
        }
        this.answers.push(answer)
        this.clicks += 1

        if (this.debug) console.log(answer)
        this.setState({ waiting: true })
    }

    lastScore = (distancia) => {
        if (this.answers.length === 0) {
            return 0
        }

        const last = this.answers[this.answers.length - 1]
        return last.score
    }

    mouseMove = (lat,long) => {
        this.setState({
            lastMove: [lat,long]
        });
    }

    handleNext() {
        // Saving data via API when using last clip
        if (this.clicks >= this.limit) {
            const data = { email: this.props.email, answers: this.answers }
            TaskData.upload(data)
                .then(response => response.json())
                .then(data => {
                    TaskData.clear()
                })
                .catch(err => {
                    window.handleError(err, '[E003] Não foi possível salvar os dados da tarefa! Entre em contato com o responsável pela ferramenta.')
                })
                .finally(() => {
                    this.props.update(this.nextState, data)
                    
                    // Leaving fullscreen mode
                    if (document.fullscreenElement) { 
                        document.exitFullscreen()
                          .then(() => console.log("Document Exited form Full screen mode"))
                          .catch((err) => console.error(err))
                    } else { 
                        document.documentElement.requestFullscreen() 
                    } 
                    
                });
            return null;
        } else {
            this.setState({ waiting: false })
            this.componentDidMount()
        }
    }

    render() {
        const { lastMove, randomPos } = this.state

        const progress = (this.clicks * 100) / this.limit;
        const score = this.lastScore() * 100;
        return (
            <div onLoad={this.openMap}>
                <BorderLinearProgress
                    variant="determinate"
                    value={progress} />

                <div id="earth_div">
                </div>

                {/* <div onClick={this.click} onMouseMove={this.mouseMove}></div> */}

                <div className="positions">
                    <div>Target: {randomPos[0].toFixed(2)}, {randomPos[1].toFixed(2)}</div>
                    <div>Mouse pos: {lastMove[0].toFixed(2)}, {lastMove[1].toFixed(2)}</div>
                </div>

                {this.state.waiting && (
                    <div className={'thermometer-box'}>
                        <div>{score.toFixed(1)} %</div>
                        <Button variant="contained" onClick={this.handleNext} color="primary">
                            Avançar
                        </Button>
                    </div>
                )}
            </div>
        );
    }
}

export default Tarefa;


